$sm: 540px;
$md: 720px;
$lg: 960px;
$xl: 1140px;

#container {
  overflow: hidden;
}

#sidebar {
  background: #001223;
  padding: 0 2rem;
  @media only screen and (max-width: $lg){
    display: flex;
    flex-direction: row;
    overflow: scroll;
  }
  @media only screen and (min-width: $lg){
    overflow: auto;
    height: 100vh;
  }
}

.title {
  font-family: "Sohne Mono Halbfett";
  font-style: normal;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.05em;
  color: white;
}

.subtitle {
  font-family: "Sohne Mono Buch";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 135%;
  letter-spacing: 0.05em;
  color: white;
  margin: 2rem 0 1rem 0;
}

.sidebar__section {
  padding: 3rem 0 4rem 0;
  @media only screen and (max-width: $lg){
    padding-top: 3rem;
    padding-bottom: 4rem;
    max-height: 100vh;
  }
}

.sidebar__section--first {
  @media only screen and (max-width: $md){
    min-width: 75vw;
    padding-top: 48px;
    padding-right: 2.5rem;
    padding-left: 0.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
  @media only screen and (min-width: $md) and (max-width: $lg){
    min-width: 30vw;
    padding-top: 48px;
    padding-right: 2.5rem;
    padding-left: 0.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
}

.sidebar__section--second, .sidebar__section--third, .sidebar__section--fourth {
  @media only screen and (max-width: $md){
    min-width: 75vw;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
  @media only screen and (min-width: $md) and (max-width: $lg){
    min-width: 40vw;
    padding-top: 48px;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
}

.sidebar__section--last {
  @media only screen and (min-width: $md){
    padding-bottom: 4rem;
  }
  @media only screen and (max-width: $md){
    min-width: 75vw;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
  @media only screen and (min-width: $md) and (max-width: $lg){
    min-width: 40vw;
    padding-top: 48px;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    border-right: 1px solid rgba(255, 255, 255, 0.8);
  }
}

.sidebar__section--border {
  @media only screen and (max-width: $lg){
    display: none;
  }
  @media only screen and (min-width: $lg){
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    margin: 0 -2rem;
  }
}

.sidebar__section--stairangle {
  @media only screen and (max-width: $lg){
    margin-top: 32px;
  }
}

/* These styles are for the toggles */
.toggle-group {
  vertical-align: center;
  font-family: "Sohne Mono Halbfett";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
}

.toggle {
  text-align: center;
  display: inline-block;
  width: 49.5%;
  height: 45px;
}

.toggle--inactive {
  box-sizing: border-box;
  background: none;
  border: 1px solid rgba(255, 255, 255, 0.5);
  color: rgba(255, 255, 255, 0.5);
}

.toggle--active {
  box-sizing: border-box;
  background: rgba(65, 234, 212, 0.82);
  color: rgba(255, 255, 255, 1);
  border: rgba(65, 234, 212, 0.82);
}

.disabled {
  opacity: 0.6;
  pointer-events: none;
}

/*These styles are for the dropdown inputs*/
.dropdown, .dropdown--red {
  font-family: "Sohne Mono Halbfett";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  border-radius: 3px;
  padding: 10px 8px;
  background: white;
  height: 45px;
  border: none;
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  position: relative;
}

.dropdown:focus {
  border: 3px solid #40ead3;
}

.dropdown--red {
  border: 3px solid red;
}

.dropdown__mm {
  width: 98%;
}

.dropdown__feet,
.dropdown__inch {
  margin-right: 6px;
  width: 28%;
  background-image: url("../images/dropdown-arrow.svg");
  background-repeat: no-repeat;
  background-position: 90% center;
}

.dropdown__fraction {
  width: 42%;
  background-image: url("../images/dropdown-arrow.svg");
  background-repeat: no-repeat;
  background-position: 90% center;
}

.dropdown:hover {
  cursor: pointer;
}

.stairangle__number {
  background: white;
  padding: 11px auto;
  border-radius: 3px;
  text-align: center;
  width: 40%;
}

/*Slider controls */

.slider__height {
  width: 100%;
}

.slider__angle {
  width: 55%;
  margin: auto 0;
}

/* The slider itself */
.slider {
  -webkit-appearance: none; /* Override default CSS styles */
  appearance: none;
  height: 2px; /* Specified height */
  background: rgba(255, 255, 255, 0.5); /* Grey background */
  outline: none; /* Remove outline */
  -webkit-transition: 0.2s; /* 0.2 seconds transition on hover */
  transition: opacity 0.2s;
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 8px; /* Set a specific slider handle width */
  height: 20px; /* Slider handle height */
  background: #41ead4; /* Green background */
  cursor: pointer; /* Cursor on hover */
}

.slider::-moz-range-thumb {
  width: 8px; /* Set a specific slider handle width */
  height: 20px; /* Slider handle height */
  background: #41ead4; /* Green background */
  cursor: pointer; /* Cursor on hover */
}

/* Scrollbar style */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #c4c4c4;
  border-radius: 1em;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
